<template>
  <v-container fluid style="color:black">
    <v-row>
      <v-col cols="3" style="background-color:">
        <v-row>
          <v-col cols="12">圖表</v-col>
          <v-col cols="12" style>
            <v-avatar
              style="cursor:pointer"
              v-for="chart in charts"
              :key="chart"
              @click="changeChart(chart)"
            >
              <v-icon color="green">{{ chart }}</v-icon>
            </v-avatar>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="9" style="background-color:">
        <Allchart :chartType="chartType"></Allchart>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Allchart from "@/components/allChart.vue";
export default {
  components: { Allchart },
  data() {
    return {
      charts: [
        "fas fa-chart-bar",
        "fas fa-chart-line",
        "fas fa-chart-pie",
        "fas fa-ring",
        "fas fa-radiation",
        "fas fa-filter",
        "fas fa-th-large",
        "fas fa-ellipsis-h"
      ],
      chartType: ""
    };
  },
  computed: {},
  methods: {
    changeChart(val) {
      this.chartType = val;
    }
  },
  mounted() {}
};
</script>
<style lang="scss">
.mdi-paperclip {
  color: red;
}
</style>
